    "css":`.buttonst {
  all: unset;
  display: flex;
  align-items: center;
  position: relative;
  padding: 0.6em 2em;
  border: mediumspringgreen solid 0.15em;
  border-radius: 0.25em;
  color: mediumspringgreen;
  font-size: 1.5em;
  font-weight: 600;
  cursor: pointer;
  overflow: hidden;
  transition: border 300ms, color 300ms;
  user-select: none;
}

.buttonst p {
  z-index: 1;
}

.buttonst:hover {
  color: #212121;
}

.buttonst:active {
  border-color: teal;
}

.buttonst::after, .button::before {
  content: "";
  position: absolute;
  width: 9em;
  aspect-ratio: 1;
  background: mediumspringgreen;
  opacity: 50%;
  border-radius: 50%;
  transition: transform 500ms, background 300ms;
}

.buttonst::before {
  left: 0;
  transform: translateX(-8em);
}

.buttonst::after {
  right: 0;
  transform: translateX(8em);
}

.buttonst:hover:before {
  transform: translateX(-1em);
}

.buttonst:hover:after {
  transform: translateX(1em);
}

.buttonst:active:before,
.buttonst:active:after {
  background: teal;
}
`
